	back(e) {
		if (!(this.state.noinet == 0)){
			window.history.pushState({panel: e.state.panel}, e.state.panel)
			return
		}
		if (this.state.activeView === "populate") {
			if (this.state.populatePanel === "populate1") {
				setTimeout(() => {

					this.setState({activeView: "glav"})
					this.setState({eid: "-9"})
					this.setState({eid: 0})
				})
				setTimeout(() => {
					this.setState({vevents: this.state.eventsview})
					this.setState({eventsview: []})
				})
				if (this.state.isreload == 1) {
					setTimeout(() => this.reload())
					this.setState({isreload: 0})

				} else if (this.state.isreload == 2) {
					setTimeout(() => this.startloading())
					setTimeout(() => this.reload())
					this.setState({isreload: 0})

				}

			} else if (this.state.populatePanel === "full") {
				setTimeout(() => {
					this.setState({populatePanel: "populate1"})
				})
			} else if (this.state.populatePanel === "edit") {
				setTimeout(() => {
					this.setState({populatePanel: "full"})
				})
			}
			return
		}
		this.setState({snackbar: null, loadDown: null})
		if ((this.state.noinet == 0) && (this.state.popout === "" || this.state.popout == null)) {

			if (this.state.activeView === "my" || this.state.activeView === "adminka") {

				if (e.state.panel === "home") {
					connect.send("VKWebAppSetLocation", {"location": "home"});

					this.setState({activePanel: e.state.panel})
					this.setState({activeView: "glav"})
				}

			} else {
				if (e.state) {
					if (e.state.panel === "my" || this.state.activeView === "adminka") {
					} else {
						var lastpanel = this.state.activePanel

						if (lastpanel === "full" || lastpanel === "populate") {
							if (this.state.activeStory === "feed") {
								setTimeout(() => {

									connect.send("VKWebAppSetLocation", {"location": "home"});
									if (this.state.eventsview.length > 0) {

										this.setState({vevents: this.state.eventsview})
										this.setState({eventsview: []})
									}
								})
							} else if (this.state.activeStory === "discover") {
								setTimeout(() => {
									this.setState({seventsview: this.state.seventsviewv})
									this.setState({seventsviewv: []})
								})
							} else if (this.state.activeStory === "messages") {
								setTimeout(() => {
									this.setState({igosview: this.state.igosviewv})
									this.setState({igosviewv: []})
								})
							} else if (this.state.activeStory === "feedP") {
								setTimeout(() => {
									connect.send("VKWebAppSetLocation", {"location": "home"});
									if (this.state.eventsview.length > 0) {

										this.setState({vevents: this.state.eventsview, activeStory: "feed"})
										this.setState({eventsview: []})

									}
								})
							}
						}
						this.setState({activePanel: e.state.panel})
						this.setState({activeView: "glav"})

					}
				} else {
					this.setState({activePanel: "home"})
					connect.send("VKWebAppSetLocation", {"location": "home"});

					window.history.pushState({panel: "home"}, 'home')


				}
			}
		} else {

			connect.send("VKWebAppSetLocation", {"location": "home"});
			window.history.pushState({panel: "home"}, 'home')

		}


	}


