	updateTheme(){
		if(document.body.attributes.getNamedItem("scheme")==null||document.body.attributes.getNamedItem("scheme")==undefined||document.body.attributes.getNamedItem("default_theme")==null||document.body.attributes.getNamedItem("default_theme")==undefined||this.state.activeView==="hello"){
			return null
		}
		if(document.body.attributes.getNamedItem("default_theme").value==null){
			return null
		}
		if(document.body.attributes.getNamedItem("default_theme").value==="null"){
			return null
		}
		console.log(this.state.theme)
		console.log("this.state.theme")

		switch (this.state.theme) {
			case 0:
				document.body.attributes.getNamedItem("schemecolor").value="white"

				document.body.attributes.getNamedItem("scheme").value=document.body.attributes.getNamedItem("default_theme").value
				switch (document.body.attributes.getNamedItem("default_theme").value) {
					case 'client_light':
						document.body.attributes.getNamedItem("schemecolor").value="white"
						this.setState({schemecolor:"white"})

						connect.send("VKWebAppSetViewSettings", {"status_bar_style": "light", "action_bar_color": "#5281B9"});

						break;
					case 'client_dark':
						document.body.attributes.getNamedItem("schemecolor").value="#19191a"
						this.setState({schemecolor:"#19191a"})

						connect.send("VKWebAppSetViewSettings", {"status_bar_style": "light", "action_bar_color": "#2C2D2F"});

						break;
					case 'bright_light':
						document.body.attributes.getNamedItem("schemecolor").value="white"
						this.setState({schemecolor:"white"})

						connect.send("VKWebAppSetViewSettings", {"status_bar_style": "dark", "action_bar_color": "#fff"});

						break;
					case 'space_gray':
						document.body.attributes.getNamedItem("schemecolor").value="#19191a"
						this.setState({schemecolor:"#19191a"})

						connect.send("VKWebAppSetViewSettings", {"status_bar_style": "light", "action_bar_color": "#191919"});

						break;
				}

				break;
			case 1:
				document.body.attributes.getNamedItem("scheme").value="client_light"
				if(document.body.attributes.getNamedItem("scheme").value==="client_light"){}else{
					this.openDefault("Ошибка","Не удалось изменить тему.")
					return}
				document.body.attributes.getNamedItem("schemecolor").value="white"
				this.setState({schemecolor:"white"})
				connect.send("VKWebAppSetViewSettings", {"status_bar_style": "light", "action_bar_color": "#5281B9"});

				break;
			case 2:
				document.body.attributes.getNamedItem("scheme").value="client_dark"
				if(document.body.attributes.getNamedItem("scheme").value==="client_dark"){}else{
					this.openDefault("Ошибка","Не удалось изменить тему.")

					return}

				this.setState({schemecolor:"#19191a"})

				document.body.attributes.getNamedItem("schemecolor").value="#19191a"

				connect.send("VKWebAppSetViewSettings", {"status_bar_style": "light", "action_bar_color": "#2C2D2F"});

				break;
			case 3:
				document.body.attributes.getNamedItem("scheme").value="bright_light"
				if(document.body.attributes.getNamedItem("scheme").value==="bright_light"){}else{
					this.openDefault("Ошибка","Не удалось изменить тему.")
					return}

				this.setState({schemecolor:"white"})

				document.body.attributes.getNamedItem("schemecolor").value="white"

				connect.send("VKWebAppSetViewSettings", {"status_bar_style": "dark", "action_bar_color": "#fff"});

				break;
			case 4:
				document.body.attributes.getNamedItem("scheme").value="space_gray"
				if(document.body.attributes.getNamedItem("scheme").value==="space_gray"){}else{
					this.openDefault("Ошибка","Не удалось изменить тему.")
					return}

				this.setState({schemecolor:"#19191a"})

				document.body.attributes.getNamedItem("schemecolor").value="#19191a"

				connect.send("VKWebAppSetViewSettings", {"status_bar_style": "light", "action_bar_color": "#191919"});

				break;
		}
		this.forceUpdate()
		this.getSort()
		this.getThemes()
		this.sse()
		this.reRegen()

	}
