	getCC(text){
		if(this.state.city==undefined) return null
		if(text==undefined) text=""
		text = text.toLowerCase()
		var t = this.state.city
		var e =(t.filter(i=>i.toLowerCase().indexOf(text) > -1)).map(this.ret2.bind(this))
		if(e.length==0){
			return (<div className="noselect" style={{ paddingTop: 30, paddingBottom: 10, color: 'gray', width:'100%',textAlign:'center',fontSize:19}}>Город не найден</div>)
		}else{
			return e
		}
	}
