    unix(time){
        var now = new Date();
        var start = new Date(time*1000);
        var hr=start.getHours()
        if(hr<10){hr="0"+hr}
        var mi=start.getMinutes()
        if(mi<10){mi="0"+mi}
        var rt=" в "+hr+":"+mi

        if(now.getDate()==start.getDate()&&now.getFullYear()==start.getFullYear()&&now.getMonth()==start.getMonth()){
            rt="Сегодня,"+rt
        }else{
            now.setDate(now.getDate()+1)
            if(now.getDate()==start.getDate()&&now.getFullYear()==start.getFullYear()&&now.getMonth()==start.getMonth()){
                rt="Завтра,"+rt
            }else{
                var dt=start.getDate()
                if(dt<10){dt=0+""+dt}
                var mn=start.getMonth()+1
                if(mn<10){mn=0+""+mn}

                rt=dt+"."+mn+"."+start.getFullYear()+","+rt
            }
        }

        return rt
    }
    async load(){
        this.setState({loadDown: <Spinner size="large" style={{marginTop: 20}}/>})

        this.setState({populate:[]})
        if(document.body.attributes.getNamedItem("scheme")!==null) {
            if (document.body.attributes.getNamedItem("scheme").value === "client_light"||document.body.attributes.getNamedItem("scheme").value === "bright_light") {
                this.setState({schemecolor: "white"})
            } else {
                this.setState({schemecolor: "#19191a"})
            }
        }
        if(this.state.events.length==0){

            var exp=""

            var xhr = new XMLHttpRequest();
            xhr.open('GET', "https://api.finebot.site/events/events.populate?token="+this.state.token, true);
            xhr.send();
            xhr.onreadystatechange=()=> {
                setTimeout(() => {
                    if (xhr.readyState != 4) return;
                    if(this.state.events.length>0) return;
                    if(xhr.readyState==4&&this.state.events.length==0){
                        var re = xhr.responseText

                        try{
                            re=JSON.parse(re)
                        }catch (e) {
                            setTimeout(()=>this.noinet())
                            return
                        }


                        var i =0
                        for(i;i<re.response.length;i++){


                            var mesto=""
                            if(re.response[i].place!==undefined){
                                mesto=re.response[i].place
                            }
                            this.state.events.push(encodeURIComponent(re.response[i].title)+"&&&"+encodeURIComponent(mesto)+"&&&"+encodeURIComponent(this.unix(re.response[i].unixtime))+"&&&"+encodeURIComponent(re.response[i].id)+"&&&0&&&"+encodeURIComponent(re.response[i].img)+"&&&"+encodeURIComponent(re.response[i].igo)+"&&&"+(this.state.events.length).toString())

                        }
                        var veve=this.state.vevents
                        if(veve!==null) {
                            if (veve.length == 0) {
                                if (re.response.length > 15) {
                                    var a = this.state.events
                                    var hj = a.slice(0, 15)
                                    hj = hj.map(this.ret.bind(this))
                                    this.setState({vevents: hj})
                                } else {
                                    var a = this.state.events
                                    var hj = a
                                    hj = hj.map(this.ret.bind(this))
                                    this.setState({vevents: hj})
                                }
                            }
                        }else{
                            if (re.response.length > 15) {
                                var a = this.state.events
                                var hj = a.slice(0, 15)
                                hj = hj.map(this.ret.bind(this))
                                this.setState({vevents: hj})
                            } else {
                                var a = this.state.events
                                var hj = a
                                hj = hj.map(this.ret.bind(this))
                                this.setState({vevents: hj})
                            }
                        }

                    }
                })
            }


        }


    }
